@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: rgb(237, 235, 235);
  overflow-x: hidden;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 110rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
  color: #1F5889;
  font-weight: 900;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/** Media Queries **/
.boton-celeste {
  background-color: #2B9CA4;
  color: #FFFFFF;
  padding: 0.5rem 2rem;
  display: inline-block;
  text-align: center;
  z-index: 10;
}
.boton-celeste:hover {
  background-color: rgb(32.4057971014, 117.5652173913, 123.5942028986);
}

.boton-azul {
  background-color: #1F5889;
  color: #FFFFFF;
  padding: 0.5rem 2rem;
  display: inline-block;
  text-align: center;
  z-index: 10;
}
.boton-azul:hover {
  background-color: rgb(21.5892857143, 61.2857142857, 95.4107142857);
}

.boton-blanco {
  background-color: #FFFFFF;
  color: #000000;
  padding: 0.5rem 2rem;
  display: block;
  text-align: center;
  z-index: 10;
}
.boton-blanco:hover {
  background-color: rgb(229.5, 229.5, 229.5);
}

.boton {
  border: none;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 2rem;
}

.contenido-centrado-estrecho {
  max-width: 300px;
  margin: 0 auto;
}

.alinear-derecha {
  display: flex;
  justify-content: end;
}

.boton-container {
  justify-content: center;
}
@media (min-width: 768px) {
  .boton-container {
    justify-content: end;
  }
}

html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
